import traOrder from '@/components/tra-order/tra-order.vue';
let AllorderList = []
export default {
	components: {
		traOrder
	},
	data() {
		return {
			CustomBar: this.CustomBar,

			tabList: [{
					state: 0,
					text: '待取货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '配送中',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '抢任务',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '有异常',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					text: '已完成',
					loadingType: 'more',
					orderList: []
				}
			],

			// tabList: ["待取货", "配送中", "抢任务", "有异常", "已完成"],
			scrollLeft: 0,
			tabCur: 0,
			top: 0,
			// orderList: [],
			loading: false,
			over: false,
			count: 0,
			modalName: null,
			leg: 0
		};
	},
	computed: {
		style() {
			let CustomBar = this.CustomBar;
			return `height:calc(100vh - ${CustomBar}px - 45px);`;
		}
	},
	onLoad: function() {
		console.log("this.CustomBar==", this.CustomBar)
		this.TabCur = 2;
		this.getqiShouOrderList()
		this.loadData()
	},
	onShow: function() {
		console.log("pages/order/home");
	},
	methods: {
		//swiper 切换
		changeTab(e) {
			this.tabCur = e.target.current;
			this.loadData('tabChange');
		},

		// 点击切换
		tabSelect(index) {
			// console.log(e);
			console.log("index", index)
			this.tabCur = index;
			// this.scrollLeft = (e.currentTarget.dataset.id - 1) * 130;
			// this.loadData()
			
		},



		// 后添加的东西
		// 订单数据请求
		getqiShouOrderList: function() {
			this.$api.qiShouOrderListApi({
				"order_status": this.tabCur,
			}).then(res => {
				console.log('数据:' + JSON.stringify(res.data));
				
				AllorderList = res.data;
				// this.leg = this.orderList.length;
				// console.log("所在当前的列表数据条数", this.leg)
			})
		},

		loadData(source) {
			//这里是将订单挂载到tab列表下
			let index = this.tabCur;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			if (source === 'tabChange' && navItem.loaded === true) {
				//tab切换只有第一次需要加载数据
				return;
			}
			if(navItem.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			
			navItem.loadingType = 'loading';

			setTimeout(()=>{
				console.log("Json+++++",Json)
				let orderList = AllorderList.filter(item => {
					//添加不同状态下订单的表现形式
					item = Object.assign(item, this.orderStateExp(item.state));
					//演示数据所以自己进行状态筛选
					if (state === 0) {
						//0为全部订单
						return item;
					}
					return item.state === state
				});
				orderList.forEach(item => {
					navItem.orderList.push(item);
				})
				console.log("navItem.orderList+++++", navItem.orderList)
				//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
				this.$set(navItem, 'loaded', true);
				
				//判断是否还有数据， 有改为 more， 没有改为noMore 
				navItem.loadingType = 'more';
			}, 600);	
		},

		/*
		 *加载更多
		 */
		toLowFun() {
			console.log("触底事件");
			var _this = this;
			if (!_this.over) {
				console.log("++++++++++++")
				console.log("++++++++++++", _this.leg)
				if (this.count < _this.leg) {
					console.log("++++++++++++OOOOOOOOOOO")
					this.loading = true;
					setTimeout(function() {
						_this.orderList = _this.orderList.concat(_this.orderList);
						_this.loading = false;
						_this.count++;
					}, 2000);
				} else {
					this.over = true;
				}
			} else {
				this.loading = false;
			}
		},

		//查看订单详情
		_goDetails(e) {
			uni.navigateTo({
				url: "/pages/order-processing/details?orderId=" + e.currentTarget.dataset.id
			});
		},
		//到店
		_InStore(e) {
			console.log("骑手到店取货", e)
		},

		//转单
		_changOrder(e) {
			console.log("转单+++++:", e.currentTarget.dataset.target)
			this.modalName = e.currentTarget.dataset.target

		},

		// 关闭模态窗口
		hideModal(e) {
			this.modalName = null
		},
		//配送
		_distribution(e) {
			console.log("点击配送", e)
			this.tabCur = 1;
			this.postqiShouOrderList()
			// 需要发送请求，提交订单状态
		},

		postqiShouOrderList: function() {
			this.$api.pqiShouOrderListApi({
				"id": 1,
				"order_status": 1,
			}).then(res => {
				console.log('post数据:' + JSON.stringify(res.data));
				this.orderList = res.data;
			})
		},
		//拨打电话
		_tel() {
			uni.makePhoneCall({
				phoneNumber: '13211616601'
			});
		},
		_position() {

		}

	}
};
